.mainHead {
  font-size: 3rem;
  text-align: center;
  background-color: rgb(199, 31, 199);
  padding: 4rem;
  color: white;
  font-weight: bolder;
  margin: 0;
}

.mainFigures {
  text-align: center;
  padding: 2rem;
  color: white;
}

.mainFiguresFigure {
  font-size: 4rem;
  font-weight: bolder;
  margin: 1rem;
}

.mainFiguresTitle {
  font-size: 2rem;
  font-weight: bold;
  margin: 1rem;
}

.mainFiguresInWords {
  font-size: 1.5rem;
  margin: 1rem;
  text-transform: capitalize;
}

/********** Large devices only **********/

@media (min-width: 1200px) {
}

/********** Medium devices only **********/

@media (min-width: 992px) and (max-width: 1199px) {
}

/********** Small devices only **********/

@media (min-width: 768px) and (max-width: 991px) {
}

/********** Extra small devices only **********/

@media (max-width: 767px) {
    .mainHead{
        font-size: 2.5rem;
    }
    .mainFiguresFigure{
        font-size: 3.5rem;
    }
    .mainFiguresTitle{
        font-size: 1.7rem;
    }
    .mainFiguresInWords{
        font-size: 1.3rem;
    }
}

/********** Super extra small devices Only **********/

@media (max-width: 620px) {
    .mainHead{
        font-size: 2.2rem;
    }
    .mainFiguresFigure{
        font-size: 3.1rem;
    }
    .mainFiguresTitle{
        font-size: 1.5rem;
    }
    .mainFiguresInWords{
        font-size: 1.1rem;
    }
}

@media (max-width: 479px) {
    .mainHead{
        font-size: 2rem;
    }
    .mainFiguresFigure{
        font-size: 3rem;
    }
    .mainFiguresTitle{
        font-size: 1.3rem;
    }
    .mainFiguresInWords{
        font-size: 1rem;
    }
}
